<template>
	<div class="container">
		<h1>This is a QR Code Generator</h1>
		<v-row>
			<v-col cols="12" md="10">
				<v-text-field
					v-model="inputCode"
					label="Solo"
					solo
					hide-details="auto"
				></v-text-field>
			</v-col>
			<v-col cols="12" md="2">
				<v-btn class="mx-1" color="black" dark x-large
					@click="generateQrCode">
					generate
                </v-btn>
			</v-col>
		</v-row>
		<span class="">Code now is : {{ value }}</span>
		<qrcode-vue class="my-4" :value="value" :size="size" level="H" />
	</div>
</template>

<script>
	import QrcodeVue from 'qrcode.vue'
	export default {
		components: {
			QrcodeVue,
		},

		data() {
			return {
				inputCode: '',
				value: 'https://gamepress.gg/arknights/',
				size: 100,
			}
		},

		methods: {
			generateQrCode() {
				this.value = this.inputCode
			}
		}
	}
</script>